/* scroll */
body {
    overflow-y: overlay;
}

::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    background: lightgrey;
}

::-webkit-scrollbar-thumb {
    background-color: grey;
    border-radius: 5px;
}

/* table antd */

thead.ant-table-thead,
th.ant-table-cell {
    background-color: #373e58 !important;
    color: white !important;
}

th.ant-table-cell,
td.ant-table-cell {
    padding: 10px !important;
}

.conclude_modal.ant-modal,
.information_modal.ant-modal {
    padding-bottom: 0px;
}

.conclude_modal .ant-picker {
    width: 100%;
}

.conclude_modal .avatar {
    display: flex;
    justify-content: center;
    margin: 30px 0;
}

.conclude_modal .ant-image-img {
    object-fit: cover;
    object-position: center;
}

.conclude_modal .avatar .ant-image-mask {
    border-radius: 100%;
}

.conclude_modal .avatar .ant-image-img {
    border-radius: 100%;
    width: 220px !important;
    height: 220px !important;
    object-fit: cover;
    object-position: center;
    text-align: center;
}

.conclude_modal span.ant-upload {
    width: 100%;
}

.conclude_modal span.anticon.anticon-plus {
    min-height: auto !important;
}

.ant-modal-content {
    padding: 0 !important;
}

.ant-modal-body {
    padding: 20px;
}

.information_modal .ant-modal-content {
    overflow-y: hidden;
}

.modal-information-end-staff .ant-tabs-nav-list {
    padding-top: 10px;
}

.information_modal .ant-tabs-nav .ant-tabs-tab,
.modal-information-end-staff .ant-tabs-nav-list .ant-tabs-tab.ant-tabs-tab-active,
.modal-information-end-staff .ant-tabs-nav-list .ant-tabs-tab {
    padding: 5px 0;
}

.ant-form-vertical .ant-form-item-label {
    padding: 0;
}

.ant-modal-header {
    background: #fff;
    padding: 15px;
    margin-bottom: 0 !important;
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9;
    -webkit-box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.1);
}

.ant-modal-close {
    background: transparent;
    padding: 15px;
    text-align: center;
    margin: 0;
    position: sticky;
    top: 0;
    right: 0;
    bottom: 0;
    float: right;
    z-index: 10;
    width: auto;
    height: auto;
    color: #cc0000;
}

.ant-modal-close:hover {
    background: transparent;
    color: #ff1a1a;
}

.conclude_modal .ant-modal-footer {
    position: sticky;
    bottom: -1px;
}

.information_modal .ant-modal-footer,
.modal-information-end-staff .ant-modal-footer {
    position: absolute;
    bottom: 0;
}

.ant-modal-footer {
    display: flex;
    justify-content: center;
    flex-direction: row-reverse;
    gap: 10px;
    background-color: #fff !important;
    padding: 15px;
    margin-top: 0;
    left: 0;
    right: 0;
    -webkit-box-shadow: 1px 0px 10px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 1px 0px 10px 0px rgba(0, 0, 0, 0.1);
}

.conclude_modal .ant-modal-body,
.information_modal .ant-modal-body {
    overflow-y: overlay;
    overflow-x: hidden;
}

.conclude_modal .ant-modal-body {
    height: 72vh;
    padding: 20px 40px;
}

.information_modal .ant-modal-body {
    height: 68vh;
    padding: 40px 60px;
}

.information_modal.modal_profile .ant-modal-body,
.modal-information-end-staff.modal_profile .ant-modal-body {
    padding: 0;
    overflow: unset;
}

.modal_profile .ant-tabs-nav-wrap {
    padding: 30px 0 0 20px;
    overflow-y: overlay;
    overflow-x: hidden;
    height: 68vh;
}

.modal_profile .ant-tabs-content-holder {
    padding: 30px 20px 30px 0;
    overflow-y: overlay;
    overflow-x: hidden;
    height: 68vh;
}

@media (min-width: 1200px) {
    .information_modal .ant-modal-body,
    .modal_profile .ant-tabs-content-holder,
    .modal_profile .ant-tabs-nav-wrap {
        height: 72vh;
    }
}

@media (min-width: 1600px) {
    .information_modal .ant-modal-body,
    .modal_profile .ant-tabs-content-holder,
    .modal_profile .ant-tabs-nav-wrap {
        height: 76vh;
    }
}

@media (min-width: 1900px) {
    .information_modal .ant-modal-body,
    .modal_profile .ant-tabs-content-holder,
    .modal_profile .ant-tabs-nav-wrap {
        height: 79vh;
    }
}

.conclude_modal .ant-modal-content,
.information_modal .ant-modal-content,
.modal-information-end-staff .ant-modal-content {
    overflow: hidden;
    height: 90vh;
}

/* right modal */

.conclude_modal .ant-form-item-control {
    width: 100%;
}

.conclude_modal {
    width: 100%;
}

.conclude_modal .ant-col {
    min-height: auto;
}

.ant-collapse {
    background-color: transparent;
}

.ant-collapse.ant-collapse-icon-position-end {
    border: none;
}

.ant-collapse .ant-collapse-content {
    border: 1px solid #d9d9d9;
}

.ant-collapse > .ant-collapse-item {
    padding-bottom: 10px;
    border: none;
}

.conclude_modal .ant-collapse-item > .ant-collapse-header {
    background-color: rgba(34, 42, 69, 0.9);
    color: white !important;
    font-size: 18px;
    border-radius: 8px !important;
    align-items: center !important;
}

.conclude_modal .ant-collapse-item.ant-collapse-item-active > .ant-collapse-header {
    border-radius: 8px 8px 0 0 !important;
}

/* modal profile */

.account-information {
    width: 100%;
    display: flex;
    flex-direction: column;
    border: 1px solid #e6e6e6;
    border-top: none;
    -webkit-box-shadow: 0px 15px 15px -10px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 15px 15px -10px rgba(0, 0, 0, 0.1);
}

.ant-collapse-content-box {
    padding: 10px !important;
}

.conclude_modal .ant-form-item {
    padding: 0 10px;
}

.information_modal .ant-form-item {
    margin-bottom: 10px;
}

.top-form {
    padding: 15px 20px;
    font-size: 10px;
    font-weight: bold;
    text-transform: uppercase;
}

.account-information label,
.ant-form label,
.information_modal label {
    font-weight: normal;
}

.account-information .content-form {
    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
    padding: 20px 10px;
}

.content-form .conclude_modal {
    padding: 10px;
}

.bottom-form {
    height: 80px;
}

.name-user {
    text-align: center;
}

.cancel-form {
    background-color: #ff9933;
}

.cancel-form:hover {
    background-color: #ffb366 !important;
}

.ant-modal-footer .ant-btn-default {
    background-color: #ff9933;
    border: none;
    color: white;
}

.ant-modal-footer .ant-btn-default:not(:disabled):hover {
    background-color: #ffb366 !important;
    border: none;
    color: white;
}

.ant-modal .ant-modal-footer .ant-btn + .ant-btn:not(.ant-dropdown-trigger) {
    margin-inline-start: 0;
}

.ant-modal-footer .ant-btn-primary {
    background-color: #cc0000;
}

.ant-modal-footer .ant-btn-primary:not(:disabled):hover {
    background-color: #ff1a1a;
}

.add-form {
    background-color: #0000ff;
    margin: 0 5px;
}

.add-form:hover {
    background-color: #3333ff !important;
}

/* information modal */
.information_modal .conclude_modal {
    width: 100%;
}

.information_modal .ant-modal-footer .ant-btn-primary,
.modal-information-end-staff .ant-modal-footer .ant-btn-primary {
    background-color: #0d5ac3;
}

.information_modal .ant-modal-footer .ant-btn-primary:not(:disabled):hover,
.modal-information-end-staff .ant-modal-footer .ant-btn-primary:not(:disabled):hover {
    background-color: #3365a9;
}

.btn-save {
    background-color: green !important;
    color: #fff;
}
.btn-save:hover {
    background-color: #009900 !important;
    color: #fff;
}
.btn-close {
    background-color: orange;
    color: #fff;
}
.btn-close:hover {
    background-color: #ffb833;
    color: #fff;
}
.btn-register {
    background-color: #0d5ac3;
    color: #fff;
}
.btn-register:hover {
    background-color: #3365a9;
    color: #fff;
}

.information_modal .ant-input:focus,
.modal-information-end-staff input:focus,
.information_modal .ant-input-focused {
    box-shadow: none;
}

.information_modal input,
.modal-information-end-staff input {
    border: none;
    border-radius: 0;
    border-bottom: 1px dotted #333;
    margin-left: 0;
    width: 100%;
}

.conclude_modal .ant-row.ant-form-item-row,
.form_block .ant-row.ant-form-item-row {
    display: flex;
    flex-direction: column;
}

.modal_content .ant-form-item-control-input {
    min-height: auto;
}

.ant-col.ant-form-item-label {
    text-align: start;
}

.modal_signature {
    float: right;
    margin: 30px 50px 0 0;
    text-align: center;
}

.modal_signature p {
    padding: 4px 0;
    font-weight: bold;
}

.date-signature Input {
    width: 30px;
    padding: 0 5px;
    text-align: center;
}

.date-signature Input.day {
    width: 70px;
}

.date-signature Input.year {
    width: 50px;
}

.modal_signature p:first-child {
    font-weight: normal;
}

.top-form-information,
.bottom-form-information {
    display: flex;
    justify-content: space-between;
    border: 1px solid #cdcdcd;
    padding: 10px 0;
}

.bottom-form-information {
    margin-top: 30px;
}

.top-left-form-information {
    width: 40%;
    border-right: 1px solid #cdcdcd;
    padding: 10px;
}

.top-right-form-information {
    text-align: center;
    width: 60%;
    padding: 10px;
}

.bottom-left-form-information {
    width: 50%;
    border-right: 1px solid #cdcdcd;
    text-align: center;
}

.bottom-left-form-information Input {
    width: 50%;
    text-align: center;
}

.bottom-right-form-information {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 50%;
}

.date-signature .ant-form-item {
    margin: 0;
}
